// Colors

$grey-c: (
  grey: #f1f1f1,
  grey--light: #f9f9f9,
  grey--lighten: #fdfdfd,
  grey--dark: #ececec,
  grey--darken: #cccccc,
  grey--darkest: #444444
);

$base-c: (
  black: #000000,
  white: #ffffff
);

$main-c: (
  color--primary: #63d1bb,
  color--secondary: map_get($grey-c, grey--darkest)

);

@function color($color-map, $color-value) {
  @if map-has-key($color-map, $color-value) {
    @return map-get($color-map, $color-value)
  }
}

h1 {
  color: color($main-c, color--secondary)
}
